@import url(https://fonts.googleapis.com/css?family=Raleway:200,400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body, html {
  background-color: #1A86A8;
  margin: 0;
  padding: 0;
  font-family: 'Raleway', sans-serif;
}

* {
  box-sizing: border-box;
}

.app {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(400px, 1fr));
  grid-gap: 1rem;
  gap: 1rem;
  padding: 1rem;
}

.submodule {
  padding: 1rem;
  background-color: white;
}

button {
  background-color: white;
  outline: none;
  border-radius: 8px;
  padding: 8px; 
  width: 150px;
  color: #1A86A8;
  border: 1px dotted #1A86A8;
}

button:hover {
  border: 1px dotted white;
  font-weight: 800;
  background-color: #1A86A8;
  color: white;
}

h1 {
  text-align: center;
  font-weight: 200;
  font-size: 2rem;
  color: white;
}

h2 {
  font-weight: 200;
  color: #1A86A8;
  text-align: center;
}

b {
  color:#1A86A8;
}

input[type='text'] {
  padding: 8px;
  width: 200px;
  outline: none;
  border-radius: 8px;
  border: 1px dotted #1A86A8;
}

.birds {
  padding: 0.5rem;
  margin-top: 5px;
  border: 1px dotted #1A86A8;
  border-radius: 8px;
}

.birdies {
  max-height: 40vh;
  overflow: scroll;
}
.video {
    background-color: rgba(26,134,168,0.5);;
    color: black;
    padding: 0.5rem;
    font-size: 0.8rem;
    height: 20vh;
}
.videos {
    display: grid;
    grid-gap: .2rem;
    gap: .2rem;
    grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
    max-height: 40vh;
    overflow: scroll;
    margin-top: 5px;
}


.checklist-item {
    list-style: none;
    display: grid;
    grid-template-columns: 8fr 1fr 1fr;
}

.checklist-item li.checked{
    text-decoration: line-through;
    color: #1A86A8;
}
